import React from 'react'
import { useHistory } from 'react-router-dom'

import InputRow from '../product-input-row/product-input-row.component'
import CustomInput from '../custom-input/custom-input.component'
import ProfileOrderOverview from '../profile-order-overview/profile-order-overview.component'
import ParametersTable from '../parameters-table/parameters-table.component'
import CustomCheckbox from '../custom-checkbox/custom-checkbox.component'
import ExaminationsOverview from '../examinations-overview/examinations-overview.component'

import {
    retailNames
} from '../../utils/warehouse.utils'

import {
    Profile,
    GridContainer,
    GridRow,
    Row,
    // ThreeColRow,
    ZeroMargin,
    Title,
    Container,
    OrderHeader,
    OrderButton,
    CustomSelect,
    AdminPanelContainer
} from './user.form-styles'

const UserForm = ({
    isAdmin,
    isUpdating,
    user,
    handleChange,
    handleParameterChange,
    handleCompanyChange,
    isOptometrist,
    isOrder = false,
    isCustomer
}) => {
    const { push } = useHistory()

    const handleAddOrderButtonClick = e => {
        e.preventDefault()
        push(`/dashboard/objednavky/${user._id}`)
    }


    return (
        <GridContainer>
            <Profile>
                {isAdmin && !isCustomer && <AdminPanelContainer>
                    <h2>Admin panel</h2>
                    <InputRow
                        label="Predaja na ktorej uivatel pracuje"
                        example=''
                    >
                        <CustomSelect name='premises' value={user.premises} onChange={(e) => handleChange(e)}>
                            <option value={0}>Nezadan</option>
                            {
                                retailNames.map((name, idx) => {
                                    if (idx === 0) return
                                    return (
                                        <option key={idx} value={idx}>{name}</option>
                                    )
                                })
                            }
                        </CustomSelect>
                    </InputRow>
                    <InputRow
                        label="Vka administrativnch prv"
                        example=""
                    >
                        <CustomSelect name='admin' value={user.admin} onChange={(e) => handleChange(e)}>
                            <option value={0}>Zkaznik</option>
                            <option value={1}>Predava</option>
                            <option value={2}>Vedci</option>
                        </CustomSelect>
                    </InputRow>

                    <Container>
                        <CustomCheckbox
                            label='Optometrista'
                            handleClick={() => handleChange({
                                target: {
                                    name: "optometrist",
                                    value: !user.optometrist
                                }
                            })}
                            isActive={user.optometrist}
                        />
                    </Container>
                </AdminPanelContainer>}
                <div>
                    <h2>Informcie</h2>
                    <GridRow>
                        <div>
                            <CustomInput
                                label="Meno*"
                                type='text'
                                name='fName'
                                value={user.fName ?? ""}
                                handleChange={(e) => handleChange(e)}
                            />
                        </div>
                        <div>
                            <CustomInput
                                label="Priezvisko*"
                                type='text'
                                name='lName'
                                value={user.lName ?? ""}
                                handleChange={(e) => handleChange(e)}
                            />
                        </div>
                        {isCustomer && <div>
                            <CustomInput
                                // label="Priezvisko*"
                                type='date'
                                name='birthDate'
                                value={user.birthDate}
                                handleChange={(e) => handleChange(e)}
                            />
                        </div>}
                    </GridRow>
                </div>

                <Container>
                    <h2>Kontaktn daje</h2>
                    <Row>
                        <div>
                            <CustomInput
                                label="E-mail*"
                                type='email'
                                name='email'
                                value={user.email ?? ""}
                                handleChange={(e) => handleChange(e)}
                            />
                        </div>
                        {isCustomer && <div>
                            <CustomInput
                                label="Telefnne slo"
                                type='text'
                                name='phone'
                                value={user.phone ?? ""}
                                handleChange={(e) => handleChange(e)}
                            />
                        </div>}
                    </Row>
                </Container>

                {isCustomer && <Container>
                    <h2>Fakturan daje</h2>
                    <GridRow>
                        <ZeroMargin>
                            <CustomInput
                                label="Ulica a islo domu"
                                type='text'
                                name='address'
                                value={user.address ?? ""}
                                handleChange={(e) => handleChange(e)}
                            />
                        </ZeroMargin>
                        <ZeroMargin>
                            <CustomInput
                                label="PS"
                                type='text'
                                name='psc'
                                value={user.psc ?? ""}
                                handleChange={(e) => handleChange(e)}
                            />
                        </ZeroMargin>
                    </GridRow>
                    <GridRow>
                        <div>
                            <CustomInput
                                label="Mesto"
                                type='text'
                                name='city'
                                value={user.city ?? ""}
                                handleChange={(e) => handleChange(e)}
                            />
                        </div>
                        <div>
                            <CustomInput
                                label="Krajina"
                                type='text'
                                name='country'
                                value={user.country ?? ""}
                                handleChange={(e) => handleChange(e)}
                            />
                        </div>
                    </GridRow>
                </Container>}
                {isCustomer && <Container>
                    <h2>Firemn daje</h2>
                    <GridRow>
                        <ZeroMargin>
                            <CustomInput
                                label="IO"
                                type='text'
                                name='ico'
                                value={user.company.ico ?? ""}
                                handleChange={(e) => handleCompanyChange(e)}
                            />
                        </ZeroMargin>
                        <ZeroMargin>
                            <CustomInput
                                label="DI"
                                type='text'
                                name='dic'
                                value={user.company.dic ?? ""}
                                handleChange={(e) => handleCompanyChange(e)}
                            />
                        </ZeroMargin>
                        <ZeroMargin>
                            <CustomInput
                                label="I DPH"
                                type='text'
                                name='icdph'
                                value={user.company.icdph ?? ""}
                                handleChange={(e) => handleCompanyChange(e)}
                            />
                        </ZeroMargin>
                        <ZeroMargin>
                            {/* <CustomInput
                                label="I DPH"
                                type='text'
                                name='icdph'
                                value={user.company.icdph ?? ""}
                                handleChange={(e) => handleCompanyChange(e)}
                            /> */}
                        </ZeroMargin>
                    </GridRow>
                    <GridRow>
                        <ZeroMargin>
                            <CustomInput
                                label="Ulica a islo domu"
                                type='text'
                                name='address'
                                value={user.company.address ?? ""}
                                handleChange={(e) => handleCompanyChange(e)}
                            />
                        </ZeroMargin>
                        <ZeroMargin>
                            <CustomInput
                                label="PS"
                                type='text'
                                name='psc'
                                value={user.company.psc ?? ""}
                                handleChange={(e) => handleCompanyChange(e)}
                            />
                        </ZeroMargin>
                    </GridRow>
                    <GridRow>
                        <div>
                            <CustomInput
                                label="Mesto"
                                type='text'
                                name='city'
                                value={user.company.city ?? ""}
                                handleChange={(e) => handleCompanyChange(e)}
                            />
                        </div>
                        <div>
                            <CustomInput
                                label="Krajina"
                                type='text'
                                name='country'
                                value={user.company.country ?? ""}
                                handleChange={(e) => handleCompanyChange(e)}
                            />
                        </div>
                    </GridRow>
                </Container>}

                {isCustomer && <Container>
                    <h2>Parametre</h2>

                    <ParametersTable
                        parameters={user.lenses}
                        handleChange={handleParameterChange}
                    />
                </Container>}


            </Profile>

            {
                isCustomer && !isOrder && isUpdating && <div>
                    <OrderHeader>
                        <Title>
                            Objednvky
                    </Title>
                        <OrderButton onClick={e => handleAddOrderButtonClick(e)}>Prida objednvku</OrderButton>
                    </OrderHeader>
                    <div>
                        {user.orders.length > 0 ? user.orders.map((order, idx) => (
                            <ProfileOrderOverview
                                key={idx}
                                id={order}
                                handleUpdateClick={() => push(`/dashboard/objednavky/${user._id}/${order}`)}
                            />
                        )).reverse().slice(0, 5) : (
                            <p>iadn objednvky</p>
                        )}
                    </div>
                    {isOptometrist && <ExaminationsOverview />}
                </div>
            }
        </GridContainer >
    )
}

export default UserForm
